.p-dashboard .content--wrapper {
  padding-top: var(--l-05); }

.p-dashboard-header {
  justify-content: space-between;
  display: flex;
  padding: 0 var(--l-01);
  margin-bottom: var(--l-06); }
  .p-dashboard-header time {
    text-align: right;
    font-weight: 700; }

.p-dashboard-content {
  display: flex; }
  .p-dashboard-content .content--places {
    padding: 0 var(--l-03) 0 var(--l-01);
    flex: 1; }
  .p-dashboard-content .content--places header {
    display: flex;
    justify-content: space-between; }
  .p-dashboard-content .content--schedule {
    padding: 0 var(--l-01) 0 var(--l-03);
    flex: 1; }
