.Footer--wrapper nav {
  padding: 0 var(--l-05); }

.Footer--wrapper .links--wrapper {
  padding: var(--l-03) var(--l-03);
  border-top: 2px solid var(--green-transparent);
  display: flex;
  justify-content: flex-end; }

.Footer--wrapper .col-left {
  padding-right: var(--l-03);
  border-right: 2px solid var(--green-transparent); }

.Footer--wrapper .col-right {
  padding-left: var(--l-03); }

.Footer--wrapper li {
  font-size: var(--txt-xsm);
  color: var(--green-transparent);
  margin-bottom: var(--c-02); }
  .Footer--wrapper li.title {
    color: var(--black);
    margin-bottom: var(--c-05); }
  .Footer--wrapper li a {
    color: var(--green-transparent); }

.Footer--wrapper .legal-info {
  background-color: var(--green-primary); }
  .Footer--wrapper .legal-info .legal-info--content {
    padding: var(--l-01) var(--l-05);
    color: var(--white);
    font-size: var(--txt-xsm);
    text-align: left; }
